@import url(https://fonts.googleapis.com/css?family=Numans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(4deg, #8b777794, transparent);
  background: linear-gradient(86deg, #8b777794, transparent);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
ul{
  list-style: none;
  margin: 0;
  padding:0;
}
img{
  max-width: 100%;
  height: auto;
}
.loader{
    background: #00000078;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
    color: #fff;
    font-size: 20px;
}
.container{
    max-width: 1140px;
    margin: 0 auto;
    position: relative;
}
header{
    text-align: right;
    background: -webkit-linear-gradient(45deg, transparent, #0c5a5e);
    background: linear-gradient(45deg, transparent, #0c5a5e);
}
header > nav > ul {
    display: inline-flex;
    justify-content: space-around;
}
header > nav > ul > li > a {
    padding: 10px 12px;
    display: inline-block;
    color: #fff;
}
a:hover{
    text-decoration: none;
    color: #fff;
}

