.card {
  height: 370px;
  margin-top: auto;
  margin-bottom: auto;
  width: 400px;
  background-color: rgba(0, 0, 0, 0.5) !important;
}

.social_icon span {
  font-size: 60px;
  margin-left: 10px;
  color: #ffc312;
}

.social_icon span:hover {
  color: white;
  cursor: pointer;
}

.card-header h3 {
  color: white;
}

.social_icon {
  position: absolute;
  right: 20px;
  top: -45px;
}

.input-group-prepend span {
  width: 50px;
  background-color: #ffc312;
  color: black;
  border: 0 !important;
}

input:focus {
  outline: 0 0 0 0 !important;
  box-shadow: 0 0 0 0 !important;
}

.remember {
  color: white;
}

.remember input {
  width: 20px;
  height: 20px;
  margin-left: 15px;
  margin-right: 5px;
}

.login_btn {
  color: black;
  background-color: #ffc312;
  width: 100px;
}

.login_btn:hover {
  color: black;
  background-color: white;
}

.links {
  color: white;
}

.links a {
  margin-left: 4px;
}
.vh100 {
  height: 100vh;
}

* {
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
  }
  
  body {
    background: #EAEBEC;
  }
  
  .calculator-container {
    width: 400px;
    margin: auto;
  }
   input{
    border:1px solid red;
  }
  
  .calcbody {
    width: 275px;
    margin: auto;
    margin-top: 30px;
    min-height: 400px;
    border: solid 1px #3A4655;
    box-shadow: 0 8px 50px -7px black;
  }
  
  .calc-screen {
    background: #3A4655;
    width: 100%;
    height: 150px;
    padding: 20px;
  }
  
  .calc-operation {
    text-align: right;
    color: #727B86;
    font-size: 21px;
    padding-bottom: 10px;
    border-bottom: dotted 1px;
  }
  
  .calc-typed {
    margin-top: 20px;
    font-size: 45px;
    text-align: right;
    color: #fff;
  }
  
  .calc-button-row {
    width: 100%;
    background: #3C4857;
  }
  
  .button {
    width: 25%;
    background: #425062;
    color: #fff;
    padding: 20px;
    display: inline-block;
    font-size: 25px;
    text-align: center;
    vertical-align: middle;
    margin-right: -4px;
    border-right: solid 2px #3C4857;
    border-bottom: solid 2px #3C4857;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
  }
  
  .button.l {
    color: #AEB3BA;
    background: #404D5E;
  }
  
  .button.c {
    color: #D95D4E;
    background: #404D5E;
  }
  
  .button:hover {
    background: #E0B612;
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  .button.no-animation:hover{
    background: #404D5E !important;
    -webkit-transform: none;
            transform: none;
  }
  
  .button.c:hover,
  .button.l:hover {
    background: #E0B612;
    color: #fff;
  }
  
  .blink-me {
    color: #E0B612;
  }
.container{
    display: flex;
    flex-wrap: wrap;
}

.imageListing{
    flex: 1 0 34%;
    position: relative;
    margin-bottom: 25px;
}
.imageListing:nth-child(even){
    margin-left: 25px;
}
.imageParent{
    position: absolute;
    top:0;
    left: 0;
    z-index: 2;
    width:100%;
}
.placeholderImage{
    -webkit-filter: blur(10px);
            filter: blur(10px);
    z-index: 1;
}
.realImage{
    z-index: 2;
}
